body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.top {
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100vw;
  height: 100%;
  max-height: 9vh;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.9)), to(rgba(255,255,255,0.5)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.9), rgba(255,255,255,0.5));
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0.9), rgba(255,255,255,0.5));
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.9), rgba(255,255,255,0.5));
  border-bottom-style: groove;
  border-color: #000;
  border-width: 0.7vh;
}
#AlarmApp {
  margin: 0px auto;
  width: 100vw;
  top: 0;
  height: 100%;
  left: 0;
  position: fixed;
}
.top i {
  margin-top: 20px;
}
#titleInput {
  text-align: center;
}
