.clock-segment {
  width: 80%;
  margin: 0px auto !important;
  text-align: center;
}
.clock {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 40px;
  color: black !important;
  vertical-align: middle;
  // margin: 10px auto;
}
div.meta > p {
  padding-top: 10px;
}
.icon.large.plus {
  margin: 0px !important;
}
textarea {
  border: 0;
}
.event {
  border-bottom-width: thin;
  border-bottom-style: inset;
  padding-bottom: 10px !important;
}

.activeIcon {
 color: black;
}
.disabledIcon {
  color: grey;
}
.alarm-container {
  margin: 0px auto;
  padding-top: 15vh;
  overflow-y: scroll;
  max-height: 100%;
  max-width: 960px;
}

.grey-out-text {
  color: grey !important;
}
#viewPager, #frame {
  height: 90%;
}

.vh-center {
  position: relative;
 top: 45%;
 -webkit-transform: translateY(-50%);
     -ms-transform: translateY(-50%);
         transform: translateY(-50%);
}

.vh-center > div:first-child {
  padding-top: 16vh;
  margin: 0 auto;
  /*padding: 10px;*/
}
.vh-center h5 {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 3vh;
  color: #000;
}
.timeInput {
  font-size: 5.5em;
  margin: 0px auto;
  text-align: center;
  font-weight: bolder;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 60%;
  margin: 0px auto;
}
#hoursInput, #minsInput {
  width: 80%;
  text-align: center;
}
