#noteInput {
  background: transparent;
  padding: 20px;
  width: 100%;
  text-align: center;
  outline: none;
  resize: none;
}

#alarm-form-container{
  padding: 20px;
  position: fixed;
  width: 100%;
  height: 100%;
  bottom: 0px;
  top: 70px;
}
#bottom {
  left: 0px;
  right: 0px;
  margin: 0px auto;
  bottom: 0px;
  height: 20%;
  position: fixed;
  width: 100%;
  background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: -o-linear-gradient(bottom, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255, 0.7)), to(rgba(255,255,255,0)));
  background: linear-gradient(to top, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
}
#bottomButton{
  position: fixed;
  width: 100px;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0px auto;
  margin-bottom: 3%;
  background-color: white;
  border: 0.5px solid rgb(144, 144, 144);

}
#bottomButton > i {
  color: #000;
}
#swipe-view {
  height: 100%;
}
#swipeBottom {
  left: 0px;
  right: 0px;
  width: 100%;
  height: 50px;
  margin: 0px auto;
  position: fixed;
  bottom: 20px;
} p {
  color: rgb(186, 178, 182);
}
#alarmConfirmButton {
  width: 80%;
  margin: 10vh auto;
}
.DayPicker {
  width: 100%;
}
.DayPicker-Month {
  width: 95%;
  height: 50vh;
}
 #swipe-view {
   border: 1px solid black;
   max-width: 100vw;
   width: 100%;

 }

 .ui.dimmer {
   background-color: transparent !important;
 }

i.trash.large.icon{
  margin-top: 3vh !important;
}
 div.ui.segment.clock-segment {
   padding: 3vh !important;
   width: 60%;
 }
div.extra p {
  padding-bottom: 4vh !important;
}
 div.meta p {
   padding-top: 4vh !important;
 }

 #alarm-segment {
   width: 80%;
   margin: 0px auto !important;
   text-align: center;
 }

 #alarmButtons {
   width: 60%;
   margin: 0px auto;
 }

 div#alarmButtons button {
  margin-bottom: 1vh;
 }


 #pagerControls {
   margin: 0px auto;
   position: absolute;
   left: 0px;
   bottom: 0px;
   right: 0px;
   height: 15vh;
   width: 80%;
   text-align: center;
 }

 #pagerControls a {
   margin: 0px auto;
   -webkit-user-select: none;
      -moz-user-select: none;
       -ms-user-select: none;
           user-select: none;
 }

 #lobster {
   -webkit-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
    cursor: default;
   user-select: none !important;
   font-family: 'Lobster', cursive;
   font-size: 10vh;
   top: 2.9vh;
   position: absolute;
   left: 0px;
   right: 0px;
   color: black;
 }

p#snoozeText {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0px;
  margin: 0px auto;
  margin-bottom: -4vh;
}

#timeInputRow {
  padding-top: 5vh;
  padding-bottom: 5vh;
}
i.chevron.up.icon, i.chevron.down.icon {
  height: 100%;
  margin: 0px auto;
}

#incrementButtons, #decrementButtons {
  color: #d5d5d5;
}

#alarmFormSuccess div.menu {
  width: 80%;
  margin: 0px auto 5vh auto;
}

div.event div.ui.three.column.grid {
  width: 100%;
}

#currentTime {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  cursor: default;
  user-select: none !important;
  font-family: 'Lobster', cursive;
  margin: 0px auto;
  text-align: center;
  color: #fff6dd;
  text-shadow: -1px 1px 6px black;
  font-size: 1.2em;
  position: absolute;
  top: 8vh;
  left: 0;
  right: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.7)), to(rgba(255,255,255,0)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  z-index: 99 !important;
  padding-top: 1vh;
  width: 100%;
  height: 100%;
  max-height: 6vh;
}

#topHeader {

  background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: -o-linear-gradient(bottom, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.7)), to(rgba(255,255,255,0)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.7), rgba(255,255,255,0));

  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  max-height: 14vh;
}

#alarmDate {
  margin: 0px auto;
  margin-bottom: 1em !important;
  display: block;
  width: 100%;
  text-align: center;
  font-size: 1.2em;
  opacity: 0.7;
}



